@keyframes rotate-for-seconds {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stop-watch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin: auto;
  position: relative;
}

.stop-watch * {
  transform-origin: bottom;
  position: absolute;
  top: 50%;
  left: 50%;
}

.stop-watch__minutes-hand {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s linear infinite rotate-for-seconds;
  top: 25%;
  left: 48.1%;
  transform: translate(-50%, -100%);
}

.stop-watch__seconds-hand {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-for-seconds;
  top: 3%;
  left: 49%;
}

.stop-watch__center-circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  top: 50%;
  transform: translate(-50%, -50%);
}

.stop-watch--speed-up :first-child {
  animation: 600s linear infinite rotate-for-seconds;
}

.stop-watch--speed-up :nth-child(2) {
  animation: 10s linear infinite rotate-for-seconds;
}

